.App {
  text-align: center;
}

.success_msg{
	color:green;
}

.error_msg{
	color:red;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#checkout-button {
  height: 36px;
  background: #556cd6;
  color: white;
  width: 100%;
  font-size: 14px;
  border: 0;
  font-weight: 500;
  cursor: pointer;
  letter-spacing: 0.6;
  border-radius: 0 0 6px 6px;
  transition: all 0.2s ease;
  box-shadow: 0px 4px 5.5px 0px rgba(0, 0, 0, 0.07);
}
#checkout-button:hover {
  opacity: 0.8;
}


.pagination {
  margin: 15px auto;
  display: flex;
  list-style: none;
  outline: none;
}
.pagination > .active > a{
  background-color:#02006c;
  border-color: #02006c;
  color: #fff;
}
.pagination > li > a{
  border: 1px solid #02006c;
  padding: 5px 10px;
  outline: none;
  cursor: pointer;
  margin-right: 6px;
}
.pagination > .active > a, .pagination > .active > span, .pagination > .active > a:hover, .pagination > .active > span:hover, .pagination > .active > a:focus, .pagination > .active > span:focus{
  background-color: #02006c;
  border-color: #02006c;
  outline: none ;
  color: #ffffff
}
.pagination > li > a, .pagination > li > span{
  color: #02006c;
}
.pagination > li:first-child > a, .pagination > li:first-child > span, .pagination > li:last-child > a, .pagination > li:last-child > span{
  border-radius: unset
}

.FormGroup {
	padding: 0;
	border-style: none;
	will-change: opacity, transform;
}

.AppWrapper{
	max-width: 500px;
    margin: 50px auto;
    background-color: #02006c;
    padding: 40px;
    color: #fff;
}

.FormRow label{
    display: block;
    width: 100%;
    padding: 6px 0;
}

.FormRow input{
    display: block;
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
	margin-bottom: 6px;
}

.AppWrapper .Form {
	overflow: hidden;
}

.FormRow {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	align-items: center;
    flex-wrap: wrap;
}

.StripeElement--webkit-autofill {
	background: transparent !important;
}

.StripeElement {
	width: 100%;
	padding: 11px 15px 11px 0;
	margin: 20px 0;
}

.SubmitButton {
	background-color:#ffffff;
	border: 0px;
	padding:8px 20px;
	border-radius: 4px;
	text-align: center;
	margin: 0 auto;
	display: inline-block;
}
